













import Vue from "vue";
import lottie, { AnimationItem } from "lottie-web";
import { WindowHelper } from "../../../../../common/shrimp/helpers/windowHelper";

export default Vue.extend({
  name: "QAnimation",
  data() {
    return {
      lottieInstance: lottie,
      lottieComp: null as unknown as AnimationItem,
    };
  },
  props: {
    _renderingProps: {
      type: Object,
    },
    src: {
      type: [String, Object],
    },
    loop: {
      type: Boolean,
      default: true
    },
    autoplay: {
      type: Boolean,
      default: true
    },
    speed: {
      type: Number,
      default: 1
    },
    direction: {
      type: String,
      default: "right"
    },
    position: {
      type: String,
    },
    zIndex: {
      type: String,
    },
    top: {
      type: String,
    },
    bottom: {
      type: String
    },
    left: {
      type: String,
    },
    right: {
      type: String,
    }
  },
  
  computed : {
    showTemplate(){
      return this.src ? false: !!this._renderingProps.isEditMode; 
    },
    lottieStyle() {
      return{
        position: this.position as "static" | "absolute" | "relative" | "fixed",
        top: this.top,
        bottom: this.bottom,
        left: this.left,
        right: this.right,
        zIndex: this.zIndex
      }
    },
  },

  watch: {
    src(newValue) {
      console.log(newValue);
      this.lottieComp?.destroy();
      this.loadAnimation(newValue);
    },
    speed(newSpeedValue){
      this.setSpeedMethod(newSpeedValue);
    },
    direction(newDirectionValue){
      this.setDirectionMethod(newDirectionValue);
    },
    loop(newLoopValue){
      this.setloopMethod(newLoopValue);
    },

  },

  mounted() {
    this.loadAnimation(this.src);
    this.setSpeedMethod(this.speed);
    this.setDirectionMethod(this.direction as "left" | "right");
  },
  methods: {
    loadAnimation(src: string | object) {
      if (typeof src === "string") {
        var url = src.startsWith("http") ? new URL(src).href : (new URL(src, WindowHelper.Instance.Window.location?.href)).href;
        this.lottieComp = this.lottieInstance.loadAnimation({
          container: this.$refs.animationContainer as HTMLElement,
          renderer: "svg",
          loop: this.loop,
          autoplay: this.autoplay,
          path: url,
        });
        return;
      }
      this.lottieComp = this.lottieInstance.loadAnimation({
        container: this.$refs.animationContainer as HTMLElement,
        renderer: "svg",
        loop: this.loop,
        autoplay: this.autoplay,
        animationData: src,
      });
    },
    play(){
      this.lottieComp.play();
    },
    stop(){
      this.lottieComp.stop();
    },
    pause(){
      this.lottieComp.pause();
    },
    setSpeedMethod(setSpeedData: number){
      if (typeof(setSpeedData) === "number" && setSpeedData !== undefined && setSpeedData !== null) {
        this.lottieComp.setSpeed(setSpeedData);
      } 
    },
    setDirectionMethod(direction: "left" | "right"){
      direction === "left" ? this.lottieComp.setDirection(-1, ) : this.lottieComp.setDirection(1, );
    },
    setloopMethod(loopData: boolean){
      if (typeof(loopData) === "boolean" && loopData !== undefined && loopData !== null) {
        this.lottieComp.setLoop(loopData)
      } 
    },
    hibernate() {
      this.lottieComp.pause();
    },
    resurrect() {
      this.lottieComp.play();
    },
  },  
  destroyed() {
    this.lottieComp?.destroy();
  },
});
